import React, { Component } from 'react';
// import {Link} from 'react-router';
// import {Link} from 'react-bootstrap';

import logo from '../../imgs/logo-know-me.png';
import phoneImg1 from '../../imgs/phone-img.png';
import phoneImg2 from '../../imgs/pone-img-2.png';
import aplicate from '../../imgs/desk-white-point.png';
import taltal from '../../imgs/taltal-1.png';
import aplicate1 from '../../imgs/mobile-white-point.png';
import x from '../../imgs/x-1.png'
import x1 from '../../imgs/x-2.png'
import Navbar from '../navbar/navbar';
import './header.css'

class Header extends Component {
    render() {
        return (
            <div>
                <Navbar></Navbar>
                <div className="start-know-me container-fluid" >
                    <div className="row ">
                        <img className="background-img"></img>
                        <div className="logo-and-text col-md-4 col-sm-8 m-0 p-0">
                            <img className="logo-know-me" src={logo}></img>
                            <div className="describe-know-me" >
                                <p className="describe-know-me-1">
                                    The pepole behind<br></br>
                                    the business
                                </p>
                                <p className="describe-know-me-2">
                                    Instant Digital Business Card
                                </p>
                            </div>
                            <a href="https://knowme.page/login/">
                                <button className="lets-start-btn " >
                                    <div className="d-flex justify-content-center align-items-center">
                                        <p className="lets-start-text">Let's Start To Knowme Now!</p>
                                        <div className="d-none d-sm-block triangular"></div>
                                        <div className="triangular d-block d-md-none"></div>
                                    </div>
                                </button>
                            </a>
                        </div>
                        <div>
                            <img className="aplicate d-none d-sm-block" src={aplicate}></img>
                            <img className="aplicate1 d-block d-md-none" src={aplicate1}></img>
                            <div className="col-sm-4 d-none d-sm-block">
                                <img src={phoneImg1} className=" phone-img-1"></img>
                            </div>

                            <div className=" d-block d-md-none">
                                <img src={phoneImg2} className=" phone-img-2"></img>
                            </div>

                            <div className="circle circle-1 d-none d-sm-block"></div>
                            <div className="circle circle-2"></div>
                            <img className="taltal" src={taltal}></img>
                            <img src={x} className="x d-block d-sm-none"></img>
                            <img src={x1} className="x d-none d-sm-block"></img>
                        </div>
                    </div>
                </div>
            </div>);

    }
}


export default Header;