import React, { Component } from 'react'
import logoNavBar from '../../imgs/logo-desk.png'
import logoNavBar1 from '../../imgs/logo-mobile.png'
import nav from '../../imgs/navigate.png'
import './navbar.css'
class navbar extends Component {
    render() {
        return (
            <div className="container-fluid d-flex justify-content-center">
                <nav className="navbar m-0 p-0 d-flex justify-content-between">
                    <div className="d-flex align-items-center">
                        <img src={logoNavBar} className="logo-nav-bar d-none d-sm-block " ></img>
                        <img src={logoNavBar1} className=" d-block d-md-none"></img>
                    </div>
                    <div className=" d-none d-sm-block">
                        <a href="https://knowme.page/login/">
                            <button className="sign-in" >SIGN IN</button>
                        </a>
                        <a href="https://knowme.page/login/">
                            <button className="sign-up-btn" >SIGN UP</button>
                        </a>
                    </div>
                    <div className="btns-sighn-in-nav col-8-align-self-end d-block d-sm-none">
                        <a href="https://knowme.page/login/">
                            <button className="sign-in-nav">SIGN IN \</button>
                        </a>
                        <a href="https://knowme.page/login/">
                            <button className="sign-up-btn-nav" >SIGN UP</button>
                        </a>
                        <img src={nav} ></img>
                    </div>
                </nav>
            </div>
        );
    }
}

export default navbar;